import React, { useState } from "react";
import readXlsxFile from "read-excel-file";
import $ from "jquery";
import Home from "../home/index";

const Dashboard = () => {
  const [pieGraphOneData, setpieGraphOneData] = useState([]);
  const [pieGraphTwoData, setpieGraphTwoData] = useState([]);
  const [pieGraphThreeData, setpieGraphThreeData] = useState([]);
  const [pieGraphFourData, setpieGraphFourData] = useState([]);
  
  const [barGraphOneData, setbarGraphOneData] = useState([]);
  const [barGraphTwoData, setbarGraphTwoData] = useState([]);
  const [barGraphThreeData, setbarGraphThreeData] = useState([]);
  const [barGraphFourData, setbarGraphFourData] = useState([]);
  const [barGraphFiveData, setbarGraphFiveData] = useState([]);
  const [barGraphSixData, setbarGraphSixData] = useState([]);


  const [isDataUpdaloaded, setisDataUpdaloaded] = useState(false);
  

  const processPieChartData = (label, data, header= 'graph') => {
    let pieData = {
      labels: label,
      datasets: [
        {
          label: "Net Profit analysis",
          backgroundColor: [
            'rgba(255, 99, 132, 0.6)',
            'rgba(54, 162, 235, 0.6)',
            'rgba(255, 206, 86, 0.6)',
            'rgba(75, 192, 192, 0.6)',
            'rgba(153, 102, 255, 0.6)',
            'rgba(255, 159, 64, 0.6)',
            'rgba(255, 99, 132, 0.6)'
          ],
          hoverBackgroundColor: [
            'rgba(255, 99, 132, 0.6)',
            'rgba(54, 162, 235, 0.6)',
            'rgba(255, 206, 86, 0.6)',
            'rgba(75, 192, 192, 0.6)',
            'rgba(153, 102, 255, 0.6)',
            'rgba(255, 159, 64, 0.6)',
            'rgba(255, 99, 132, 0.6)'
          ],
          borderWidth: 2,
          data: data,
        },
      ],
    };
    return pieData;
  }
  const processBarChartData = (label, data, header= 'graph') => {
    let barData = {
      labels: label,
      datasets: [
        {
          label: header,
          backgroundColor: [
            'rgba(255, 99, 132, 0.6)',
            'rgba(54, 162, 235, 0.6)',
            'rgba(255, 206, 86, 0.6)',
            'rgba(75, 192, 192, 0.6)',
            'rgba(153, 102, 255, 0.6)',
            'rgba(255, 159, 64, 0.6)',
            'rgba(255, 99, 132, 0.6)'
          ],
          borderColor: [
            'rgba(255, 99, 132, 0.6)',
            'rgba(54, 162, 235, 0.6)',
            'rgba(255, 206, 86, 0.6)',
            'rgba(75, 192, 192, 0.6)',
            'rgba(153, 102, 255, 0.6)',
            'rgba(255, 159, 64, 0.6)',
            'rgba(255, 99, 132, 0.6)'
          ],
          borderWidth: 2,
          data: data,
        },
      ],
    };
    return barData;
  }

  const onChangeHandler = async (event) => {
    try {
      readXlsxFile(event.target.files[0]).then((rows) => {
        
        if (rows && rows.length > 0) {
          let label = [],
          barGeaphLabel2 = [],
          barGeaphLabel3 = [],
          data = [], 
          barGeaphData2 = [],
          barGeaphData3 = [],
          pieLabel = [],
          pieData = [],
          pieLabelOne = [],
          pieDataOne = [];
          
          for (let index = 0; index < rows.length; index++) {
            const element = rows[index];
            if (index > 2 && index < 9) {
              label.push(element[0]);
              data.push(element[1]);
            }
            // COGS & GP Analysis
            if (index > 11 && index < 16) {
              barGeaphLabel2.push(element[0]);
              barGeaphData2.push(element[1]);
            }
            // Indirect Expense analysis
            if (index > 21 && index < 37) {
              barGeaphLabel3.push(element[0]);
              barGeaphData3.push(element[1]);
            }

            if (index > 38 && index < 42) {
              pieLabel.push(element[0]);
              pieData.push(element[1]);
            }
            if (index > 17 && index < 20) {
              pieLabelOne.push(element[0]);
              pieDataOne.push(element[1]);
            }
          }
          let barData =  processBarChartData(label,data, rows[1][0])
          let barData2 =  processBarChartData(barGeaphLabel2,barGeaphData2, rows[10][0])
          let barData3 =  processBarChartData(barGeaphLabel3,barGeaphData3, rows[20][0])
          
          let pieData0 = processPieChartData(pieLabel, pieData, 'aaaaaaaa')
          let pieData1 = processPieChartData(pieLabelOne, pieDataOne, 'aaaaaaaa')
          setbarGraphOneData(barData);
          setisDataUpdaloaded(true);
          setpieGraphOneData(pieData0)
          setpieGraphTwoData(pieData1)
          setbarGraphTwoData(barData2)
          setbarGraphThreeData(barData3)
        }
      });
      readXlsxFile(event.target.files[0], { sheet: 'Analysis Report 2018'}).then((rows) => {
        console.log('@@@rows', rows);
        
        if (rows && rows.length > 0) {
          let label = [],
          barGeaphLabel2 = [],
          barGeaphLabel3 = [],
          data = [], 
          barGeaphData2 = [],
          barGeaphData3 = [],
          pieLabel = [],
          pieData = [],
          pieLabelOne = [],
          pieDataOne = [];
          
          for (let index = 0; index < rows.length; index++) {
            const element = rows[index];
            if (index > 2 && index < 6) {
              label.push(element[0]);
              data.push(element[1]);
            }
            // COGS & GP Analysis
            if (index > 7 && index < 10) {
              barGeaphLabel2.push(element[0]);
              barGeaphData2.push(element[1]);
            }
            // Indirect Expense analysis
            if (index > 14 && index < 34) {
              barGeaphLabel3.push(element[0]);
              barGeaphData3.push(element[1]);
            }

            if (index > 9 && index < 12) {
              pieLabel.push(element[0]);
              pieData.push(element[1]);
            }
            if (index > 35 && index < 40) {
              pieLabelOne.push(element[0]);
              pieDataOne.push(element[1]);
            }
          }
          let barData =  processBarChartData(label,data, rows[0][0])
          let barData2 =  processBarChartData(barGeaphLabel2,barGeaphData2, rows[6][0])
          let barData3 =  processBarChartData(barGeaphLabel3,barGeaphData3, rows[13][0])
          
          let pieData0 = processPieChartData(pieLabel, pieData, 'aaaaaaaa')
          let pieData1 = processPieChartData(pieLabelOne, pieDataOne, rows[35][0])
          setisDataUpdaloaded(true);
          setbarGraphFourData(barData);
          setbarGraphFiveData(barData3)
          setbarGraphSixData(barData2)

          setpieGraphThreeData(pieData0)
          setpieGraphFourData(pieData1)
          // setpieGraphTwoData(pieData1)
          // setbarGraphTwoData(barData2)
          // setbarGraphThreeData(barData3)
        }
      });
    } catch (error) {
      console.log("@@@@error", error);
    }
  };

  const goToHome = () => {
    setisDataUpdaloaded(false);
  }

  const readURL = async (input) => {
    if (input.files && input.files[0]) {
      var reader = new FileReader();

      reader.onload = function (e) {
        $(".image-upload-wrap").hide();

        $(".file-upload-image").attr("src", e.target.result);
        $(".file-upload-content").show();

        $(".image-title").html(input.files[0].name);
      };

      reader.readAsDataURL(input.files[0]);
    } else {
      removeUpload();
    }
  };

  const removeUpload = () => {
    $(".file-upload-input").replaceWith($(".file-upload-input").clone());
    $(".file-upload-content").hide();
    $(".image-upload-wrap").show();
  };
  $(".image-upload-wrap").bind("dragover", function () {
    $(".image-upload-wrap").addClass("image-dropping");
  });
  $(".image-upload-wrap").bind("dragleave", function () {
    $(".image-upload-wrap").removeClass("image-dropping");
  });
  return (
    <React.Fragment>
      {!isDataUpdaloaded ? (
        <div className="file-upload">
          <button
            className="file-upload-btn"
            type="button"
            onClick={(e) => {
              $(".file-upload-input").trigger("click");
            }}
          >
            Add Image
          </button>

          <div className="image-upload-wrap">
            <input
              className="file-upload-input"
              type="file"
              onChange={(e) => {
                onChangeHandler(e);
              }}
            />
            <div className="drag-text">
              <h3>Drag and drop a file or select add Image</h3>
            </div>
          </div>
          <div className="file-upload-content">
            <img className="file-upload-image" src="#" alt="your image" />
            <div className="image-title-wrap">
              <button
                type="button"
                onClick={removeUpload()}
                className="remove-image"
              >
                Remove <span className="image-title">Uploaded Image</span>
              </button>
            </div>
          </div>
        </div>
      ) : (
        <Home pieGraphOneData={pieGraphOneData} pieGraphTwoData={pieGraphTwoData} barGraphOneData={barGraphOneData} barGraphTwoData={barGraphTwoData} 
        barGraphThreeData={barGraphThreeData} barGraphFourData={barGraphFourData} pieGraphThreeData={pieGraphThreeData}
        pieGraphFourData={pieGraphFourData} barGraphFiveData={barGraphFiveData} barGraphSixData={barGraphSixData}
          goToHome={goToHome}/>
      )}
    </React.Fragment>
  );
};
export default Dashboard;
