import React from "react";
// import { Card, Button, Container, Row, Col } from "react-bootstrap";
import { Bar, Pie } from "react-chartjs-2";
import {CardBody, Card, Button, Container, Row, Col} from 'reactstrap';

const Home = (props) => {
  return (
    <>
      <Container fluid>
        {props.barGraphOneData &&
          props.barGraphOneData.labels &&
          props.barGraphOneData.labels.length > 0 && (
            <>
              <Row>
                <Col>
                <Card>
                  <CardBody>
                    <Bar
                      data={props.barGraphOneData}
                      options={{ maintainAspectRatio: false }}
                      height={200}
                    />
                  </CardBody>
                </Card>
                </Col>
                <Col>
                <Card>
                  <CardBody>
                  <Pie
                  data={props.pieGraphOneData}
                  options={{ maintainAspectRatio: false }}
                />
                  </CardBody>
                </Card>
                </Col>
              </Row>
              <Row>
                <Col>
                <Card>
                  <CardBody>
                    <Bar
                      data={props.barGraphTwoData}
                      options={{ maintainAspectRatio: false }}
                      height={200}
                    />
                  </CardBody>
                </Card>
                </Col>
                <Col>
                <Card>
                  <CardBody>
                  <Pie
                  data={props.pieGraphOneData}
                  options={{ maintainAspectRatio: false }}
                />
                  </CardBody>
                </Card>
                </Col>
              </Row>
              <Row>
                <Col>
                <Card>
                  <CardBody>
                    <Bar
                      data={props.barGraphThreeData}
                      options={{ maintainAspectRatio: false }}
                      height={200}
                    />
                  </CardBody>
                </Card>
                </Col>
                <Col>
                <Card>
                  <CardBody>
                  <Pie
                  data={props.pieGraphTwoData}
                  options={{ maintainAspectRatio: false }}
                />
                  </CardBody>
                </Card>
                </Col>
              </Row>
              <Row>
                <Col>Report 2018</Col>
              </Row>
              <Row>
                <Col>
                <Card>
                  <CardBody>
                    <Bar
                      data={props.barGraphFourData}
                      options={{ maintainAspectRatio: false }}
                      height={200}
                    />
                  </CardBody>
                </Card>
                </Col>
                <Col>
                <Card>
                  <CardBody>
                  <Pie
                  data={props.pieGraphThreeData}
                  options={{ maintainAspectRatio: false }}
                />
                  </CardBody>
                </Card>
                </Col>
              </Row>
              <Row>
                <Col>
                <Card>
                  <CardBody>
                    <Bar
                      data={props.barGraphFiveData}
                      options={{ maintainAspectRatio: false }}
                      height={200}
                    />
                  </CardBody>
                </Card>
                </Col>
                <Col>
                <Card>
                  <CardBody>
                  <Pie
                  data={props.pieGraphThreeData}
                  options={{ maintainAspectRatio: false }}
                />
                  </CardBody>
                </Card>
                </Col>
              </Row>
              <Row>
                <Col>
                <Card>
                  <CardBody>
                    <Bar
                      data={props.barGraphSixData}
                      options={{ maintainAspectRatio: false }}
                      height={200}
                    />
                  </CardBody>
                </Card>
                </Col>
                <Col>
                <Card>
                  <CardBody>
                  <Pie
                  data={props.pieGraphFourData}
                  options={{ maintainAspectRatio: false }}
                />
                  </CardBody>
                </Card>
                </Col>
              </Row>
            </>
          )}
        <Button variant="primary" onClick={props.goToHome}>
          Go To Home
        </Button>{" "}
      </Container>
    </>
  );
};

export default Home;
